type Tsrc = Record<string, any>
type Tdst = Record<string, any> & {
  onStart?: () => void
  onUpdate?: (d: Record<string, number>, p: number) => void
  onEnd?: () => void
  duration?: number
}

function lerp(a: number, b: number, p: number) {
  return (b - a) * p + a
}

function tick(fn: Function) {
  return requestAnimationFrame(() => {
    fn() && tick(fn)
  })
}

const transition = (function () {
  let raf: number = -1 // 定时器id
  let count = 0
  let transitionMap = {} as Record<string, any>
  return _transition

  function _transition(src: Tsrc, dst: Tdst, curve: (x: number) => number) {
    const { $$trId } = src
    const { onStart, onUpdate, onEnd, duration = 500 } = dst

    let startTime = Date.now()

    let id: string
    if ($$trId) {
      id = $$trId
    } else {
      id = `$$trId$${++count}`
    }
    const attr = Object.keys(dst).filter(key => typeof dst[key] === 'number')
    attr.forEach(key => {
      if (src[key] == null) return

      let trIdKey = id + `_${key}`

      if (transitionMap[trIdKey]) {
        transitionMap[trIdKey] = {
          from: src[key] ?? transitionMap[trIdKey]?.from ?? 0,
          to: dst[key],
          startTime,
          duration,
          currentVal: src[key] ?? transitionMap[trIdKey]?.from,
          progress: 0,
          onStart,
          onUpdate,
          onEnd,
        }
      } else {
        transitionMap[trIdKey] = {
          from: src[key],
          to: dst[key],
          startTime,
          duration,
          currentVal: src[key],
          progress: 0,
          onStart,
          onUpdate,
          onEnd,
        }
      }
      onStart?.()
      // 进度为0执行一次
      onUpdate?.({ [key]: transitionMap[trIdKey].from }, 0)
    })

    if (raf > -1) return
    raf = tick(() => {
      let currentTime = Date.now()

      Object.keys(transitionMap).forEach(key => {
        const attr = key.split('_')[1]
        const trsItem = transitionMap[key]
        const { from, to, startTime, duration, onUpdate } = trsItem
        let x = (currentTime - startTime) / duration
        let p = curve(x)
        trsItem.currentVal = lerp(from, to, p)
        trsItem.progress = x
        onUpdate?.({ [attr]: trsItem.currentVal }, trsItem.progress)
        if (trsItem.progress >= 1) {
          delete transitionMap[key]
          onEnd?.()
        }
      })
      if (Object.keys(transitionMap).length) return true
      cancelAnimationFrame(raf)
      raf = -1
      return false
    })
  }
})()

export const linear = (src: Tsrc, dst: Tdst) => {
  return transition(src, dst, x => x)
}

export const ease = (src: Tsrc, dst: Tdst) => {
  return transition(src, dst, x =>
    x <= 0.5 ? 2 * x * x : -2 * x * x + 4 * x - 1
  )
}

export const easeIn = (src: Tsrc, dst: Tdst) => {
  return transition(src, dst, x => x ** 1.5)
}

export const easeOut = (src: Tsrc, dst: Tdst) => {
  return transition(src, dst, x => x ** 0.5)
}

export const collision = (src: Tsrc, dst: Tdst) => {
  return transition(src, dst, x => {
    if (x === 1) return 1
    for (let i = 1, m = 20; i < m; i++) {
      const a = 1 - (4 / 3) * Math.pow(0.5, i - 1)
      const b = 1 - (4 / 3) * Math.pow(0.5, i)
      if (x >= a && x <= b) {
        return Math.pow(3 * (x - (a + b) / 2), 2) + 1 - Math.pow(0.25, i - 1)
      }
    }
  })
}
