import { useEffect, useRef } from 'react'
import { CrkSyntheticEvent, Graphics } from '@mikixing/crk'
import { layout, stdStage } from '../common'
import { Stage, Group, Shape as BaseShape } from '@mikixing/crk'
import { getBackgroundData, setWheel, setRoundRect } from '../util'

interface IRect {
  x: number
  y: number
  width: number
  height: number
}

class Shape extends BaseShape {
  public _data?: Record<'name' | 'list', any>
}

export default function StackFigure() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  useEffect(() => {
    function getCorrectPos(pos: IRect, boundary: IRect, margin = 10) {
      let { x, y, width, height } = pos
      const { x: bx, y: by, width: bw, height: bh } = boundary
      if (x + width + margin > bx + bw) {
        x = bx + bw - width - margin
      }
      if (y + height + margin > by + bh) {
        y = by + bh - height - margin
      }
      return { x, y }
    }

    const opt = {
      title: {
        text: '堆叠区域图',
      },
      legend: {
        list: ['邮件营销', '联盟广告', '视频广告', '直接访问', '搜索引擎'],
      },
      size: {
        width: 800,
        height: 600,
      },
      grid: {
        left: '30',
        right: '30',
        bottom: '30',
        top: '30',
      },
      data: {
        xAxis: {
          list: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
        },
        yAxis: {
          step: 500,
        },
        series: [
          {
            name: '邮件营销',
            list: [120, 132, 101, 134, 90, 230, 210],
          },
          {
            name: '联盟广告',
            list: [220, 182, 191, 234, 290, 330, 310],
          },
          {
            name: '视频广告',
            list: [150, 232, 201, 154, 190, 330, 410],
          },
          {
            name: '直接访问',
            list: [320, 332, 301, 334, 390, 330, 320],
          },
          {
            name: '搜索引擎',
            list: [820, 932, 901, 2000, 1290, 1330, 1320],
          },
        ],
      },
    }

    class Table {
      public canvas: HTMLCanvasElement
      public title: any
      public legend: any
      public size: { width: number; height: number }
      public grid: { left: number; right: number; bottom: number; top: number }
      public data: Record<string, any>
      public target: null | Shape
      public singleUnit = 0
      public common: any = {}
      public stage: Stage
      public panelContainer: Group
      public tagContainer: Group

      private titleContainer: Group
      private contentContainer: Group
      private contentWidth: number = 0
      private contentHeight: number = 0

      constructor(canvas: HTMLCanvasElement, opt: Record<string, any>) {
        this.canvas = canvas
        this.title = opt.title || {}
        this.legend = opt.legend || {}
        this.size = opt.size || { width: 800, height: 600 }
        this.grid = opt.grid || {}
        this.data = opt.data || {}
        this.target = null
        this.singleUnit = 0

        this.stage = new Stage(this.canvas)
        this.titleContainer = new Group()
        this.contentContainer = new Group()
        this.tagContainer = new Group()
        this.tagContainer.ignoreEvent = true
        this.panelContainer = new Group()
        this.panelContainer.addChild(
          this.titleContainer,
          this.contentContainer,
          this.tagContainer
        )
        this.stage.addChild(this.panelContainer)
        this.init()
      }
      init() {
        this.common.color = this.legend.list.map((item: any, i: number) => {
          return `hsl(${(i * 200) % 360 | 0}, 50%, 70%)`
        })
        this.common.colorAlpha = this.legend.list.map(
          (item: any, i: number) => {
            return `hsl(${(i * 200) % 360 | 0}, 50%, 70%, 80%)`
          }
        )
        this.common.colorDay = this.data.xAxis.list.map(
          (item: any, i: number) => {
            return `hsl(${(i * 130) % 360 | 0}, 50%, 70%)`
          }
        )

        this.renderTitle()
        this.renderContent()
      }

      renderTitle() {
        let { left = 30, right = 30, bottom = 30, top = 30 } = this.grid
        this.titleContainer.x = left
        this.titleContainer.y = top

        let text = new Shape()
        text.graphics
          .setTextStyle({ font: '18px Arial' })
          .fillText(this.title.text)
        this.titleContainer.addChild(text)
        this.legend.list.forEach((item: any, i: number) => {
          let color = this.common.color[i]
          let box = new Group()
          let shape = new Shape()
          shape.y = -10
          let g = shape.graphics
          g.setStrokeStyle({ lineWidth: 2, color })
            .moveTo(i * 120 + 180, 9)
            .lineTo(i * 120 + 180 + 20, 9)
            .stroke()
          g.beginPath()
            .setFillStyle('#ffffff')
            .arc(i * 120 + 180 + 10, 9, 4, 0, Math.PI * 2)
            .fill()
            .stroke()

          let text = new Shape()
          text.graphics
            .setTextStyle({ font: '12px Arial' })
            .fillText(this.title.text)
          text.x = i * 120 + 180 + 27
          text.y = 4
          box.addChild(shape, text)
          this.titleContainer.addChild(box)
        })
      }
      renderContent() {
        let { left = 30, right = 30, bottom = 30, top = 30 } = this.grid
        left = +left + 30 // 30为纵坐标刻度预留的空间
        top = +top + 60 // 60为标题预留的空间
        const { width, height } = this.size
        this.contentWidth = width - left - right
        this.contentHeight = height - top - bottom

        let axisShape = new Shape()
        let axisG = axisShape.graphics

        this.contentContainer.x = left
        this.contentContainer.y = top
        this.contentContainer.addChild(axisShape)

        // 坐标轴
        // x轴
        axisG
          .setStrokeStyle({ color: '#666' })
          .moveTo(0, this.contentHeight)
          .lineTo(this.contentWidth, this.contentHeight)
          .stroke()
        const { xAxis = {}, yAxis = {}, series = [] } = this.data
        this.singleUnit = this.contentWidth / (xAxis.list.length - 1)
        xAxis.list.forEach((item: any, i: number) => {
          axisG
            .setStrokeStyle({ color: '#666' })
            .moveTo(i * this.singleUnit, this.contentHeight)
            .lineTo(i * this.singleUnit, this.contentHeight + 5)
            .stroke()

          let text = new Shape()
          text.graphics.setTextStyle({ font: '12px Arial' }).fillText(item)
          text.x = i * this.singleUnit - 12
          text.y = this.contentHeight + 20
          this.contentContainer.addChild(text)
        })
        // y轴
        let seriesData = [] as any
        // @ts-ignore
        series.forEach(({ list }, i: number) => {
          list.reduce(
            (accumulator: any[], currentValue: any, index: number) => {
              seriesData[index] = currentValue + (seriesData[index] || 0)
            },
            0
          )
        })
        let maxValue = Math.max(...seriesData)
        const { step } = yAxis as any
        let yCount = Math.ceil(maxValue / step)
        let unit = this.contentHeight / yCount

        for (let i = 0; i <= yCount; i++) {
          let count = yCount - i
          let y = count * unit
          let text = new Shape()
          // @ts-ignore
          text.graphics.setTextStyle({ font: '12px Arial' }).fillText(step * i)
          text.x = -30
          text.y = y - 6
          this.contentContainer.addChild(text)

          let lineShape = new Shape()
          lineShape.graphics
            .setStrokeStyle({ color: '#ddd' })
            .moveTo(0, y)
            .lineTo(this.contentWidth, y)
            .stroke()
          this.contentContainer.addChild(lineShape)
        }
        // 绘制折线图
        let renderData = []
        for (let i = 0; i < series.length; i++) {
          let renderItem = [] as any
          let lastRenderItem = renderData[i - 1] || []
          let obj = series[i]
          obj.list.forEach((nums: number, j: number) => {
            if (lastRenderItem[j]) {
              renderItem[j] = lastRenderItem[j] + nums
            } else {
              renderItem[j] = 0 + nums
            }
          })
          renderData[i] = renderItem
        }
        for (let i = renderData.length - 1; i >= 0; i--) {
          let list = renderData[i]
          let lastList = renderData[i - 1] || new Array()
          if (i === 0) lastList = new Array(xAxis.list.length).fill(0)
          let shape1 = new Shape()
          let shape2 = new Shape()
          shape2.addAttr('section')
          shape2._data = series[i]
          let g1 = shape1.graphics
          let g2 = shape2.graphics
          let total = yCount * step
          let color = this.common.color[i]
          let colorAlpha = this.common.colorAlpha[i]

          g2.setFillStyle(colorAlpha).fill()
          list.forEach((item: number, j: number) => {
            let y = this.contentHeight - (item / total) * this.contentHeight
            if (j === 0) {
              g1.setStrokeStyle({
                lineWidth: 2,
                color,
              }).moveTo(j * this.singleUnit, y)
              g2.moveTo(j * this.singleUnit, y)
            } else {
              g1.lineTo(j * this.singleUnit, y)
              g2.lineTo(j * this.singleUnit, y)
            }
          })
          for (let i = lastList.length - 1; i >= 0; i--) {
            let item = lastList[i]
            let y = this.contentHeight - (item / total) * this.contentHeight
            g2.lineTo(i * this.singleUnit, y)
          }
          g2.closePath()
          g2.fill()

          shape2.alpha = 0.6
          g1.stroke()
          this.contentContainer.addChild(shape1, shape2)
        }
      }
      clearTag() {
        this.tagContainer.x = 0
        this.tagContainer.y = 0
        this.tagContainer.removeAllChildren()
      }
      renderTag(data: Record<'name' | 'list', any>, x: number, y: number) {
        this.clearTag()
        let pos = getCorrectPos(
          { x, y, width: 140, height: 220 },
          {
            x: 0,
            width: this.size.width,
            y: 0,
            height: this.size.height,
          }
        )
        this.tagContainer.x = pos.x + 20
        this.tagContainer.y = pos.y
        const { name, list } = data

        let bgShape = new Shape()
        setRoundRect(bgShape.graphics, 0, 0, 140, 220, 10)
        bgShape.graphics
          .setShadow({
            shadowColor: '#3336',
            shadowOffsetX: 0,
            shadowOffsetY: 5,
            shadowBlur: 10,
          })
          .setFillStyle('#fff')
          .fill()

        const title = new Shape()
        title.graphics.setTextStyle({ font: '16px Arial' }).fillText(name)
        title.x = 20
        title.y = 20
        this.tagContainer.addChild(bgShape, title)

        list.forEach((item: any, i: number) => {
          let container = new Group()
          let shape = new Shape()
          let g = shape.graphics
          g.setFillStyle(this.common.colorDay[i])
            .arc(10, 2, 4, 0, Math.PI * 2)
            .fill()

          let text1 = new Shape()
          text1.graphics
            .setTextStyle({ font: '12px Arial' })
            .fillText(this.data.xAxis.list[i])
          text1.x = 20
          text1.y = 6
          let text2 = new Shape()
          text2.graphics.setTextStyle({ font: '12px Arial' }).fillText(list[i])
          text2.x = 60
          text2.y = 6
          container.addChild(shape, text1, text2)
          container.x = 20
          container.y = (i + 1) * 25 + 18
          this.tagContainer.addChild(container)
        })
      }
    }

    const table = new Table(canvasRef.current as HTMLCanvasElement, opt)
    table.stage.enableMouseOver()
    table.stage.delegate('mouseover', 'section', (ev: CrkSyntheticEvent) => {
      const { target } = ev
      ;(target as Shape).alpha = 1
      if (table.target) {
        table.target.alpha = 0.6
      }
      table.target = target as Shape

      const tp = table.tagContainer.parent.global2local(ev.x, ev.y)
      table.renderTag(
        (target as Shape)?._data ?? { name: '', list: [] },
        tp.x,
        tp.y
      )
      ticker.needsUpdate = true
    })
    table.stage.delegate('mouseout', 'section', (ev: CrkSyntheticEvent) => {
      if (!table.target) return
      table.clearTag()
      table.target.alpha = 0.6
      table.target = null
      ticker.needsUpdate = true
    })
    let { width, height, ticker, dispose } = stdStage(table.stage, {
      onResize: (ev, w, h) => {
        width = w
        height = h
        table.panelContainer.set(
          getBackgroundData(970, 750, width, height, {
            paddingTop: 30,
            paddingBottom: 30,
          })
        )
        ticker.needsUpdate = true
      },
    })

    table.panelContainer.set(
      getBackgroundData(970, 750, width, height, {
        paddingTop: 30,
        paddingBottom: 30,
      })
    )

    const removeWheel = setWheel(table.panelContainer, () => {
      ticker.needsUpdate = true
    })

    ticker.on('frame', () => {
      table.stage.update()
    })
    ticker.needsUpdate = true

    return dispose
  }, [canvasRef])
  return (
    <layout.CanvasBox>
      <canvas ref={canvasRef}></canvas>
    </layout.CanvasBox>
  )
}
